import React from "react";
import { Dialog, DialogContent, DialogTitle, Button,Typography } from "@material-ui/core";
import { battlefield, nomansland } from "./Encounters";
class App extends React.Component {
  
  state = {
    d100: null,
    rolltype: "none"
  };
  render() {
    return (
      <Dialog fullScreen={true} open={true}>
        <DialogContent>
          <DialogTitle style={{ textAlign: "center" }}>
            Generator zdarzeń OnlyWar{" "}
          </DialogTitle>
          <div style={{width:"100%",textAlign:"center"}}>
          <Button
            onClick={() =>
              this.setState({
                rolltype: "battlefield",
                d100: Math.floor(Math.random() * 100) + 1
              })
            }
          >
            Rzuć kością (Pole bitwy)
          </Button>
          <Button
            onClick={() =>
              this.setState({
                rolltype: "nomansland",
                d100: Math.floor(Math.random() * 100) + 1
              })
            }
          >
            Rzuć kością (Ziemia niczyja)
          </Button>
          </div>
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <Typography style={{textAlign:"center"}} variant="h4">
          {this.state.rolltype === "battlefield" &&
            this.state.d100 !== null &&
            battlefield(this.state.d100).string}

          {this.state.rolltype === "nomansland" &&
            this.state.d100 !== null &&
            nomansland(this.state.d100).string}
            </Typography>
        </DialogContent>
      </Dialog>
    );
  }
}

export default App;
